h scroll for people that prefer not to see them */\n@media (prefers-reduced-motion: reduce) {\n  html:focus-within {\n    scroll-behavior: auto;\n  }\n\n  *,\n  *::before,\n  *::after {\n    animation-duration: 0.01ms !important;\n    animation-iteration-count: 1 !important;\n    transition-duration: 0.01ms !important;\n    scroll-behavior: auto !important;\n  }\n}\n",".button {\n  pointer-events: auto;\n  cursor: pointer;\n  background: #e7e7e7;\n  border: none;\n  padding: 1.5rem 3rem;\n  margin: 0;\n  font-family: inherit;\n  font-size: inherit;\n  position: relative;\n  display: inline-block;\n}\n\n.button::before,\n.button::after {\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n}\n\n.button--hyperion {\n  font-family: input-mono-narrow, monospace;\n  font-weight: 500;\n  padding: 1rem 1.5rem;\n  // border: 1px solid #000;\n  overflow: hidden;\n  color: #fff;\n}\n\n.button--hyperion span {\n  display: block;\n  position: relative;\n}\n\n.button--hyperion > span {\n  overflow: hidden;\n}\n\n.button--hyperion > span > span {\n  overflow: hidden;\n  // mix-blend-mode: difference;\n}\n\n.button--hyperion:hover > span > span {\n  animation: MoveUpInitial 0.2s forwards, MoveUpEnd 0.2s forwards 0.2s;\n}\n\n@keyframes MoveUpInitial {\n  to {\n    transform: translate3d(0, -105%, 0);\n  }\n}\n\n@keyframes MoveUpEnd {\n  from {\n    transform: translate3d(0, 100%, 0);\n  }\n  to {\n    transform: translate3d(0, 0, 0);\n  }\n}\n\n.button--hyperion::before {\n  content: \"\";\n  background: #000;\n  transition: transform 0.3s cubic-bezier(0.7, 0, 0.2, 1);\n  transform-origin: 100% 50%;\n}\n\n.button--hyperion:hover::before {\n  transform: scale3d(0, 1, 1);\n  transform-origin: 0% 50%;\n}\n",".nesting {\n  .card {\n    background: yellow;\n\n    .cta & {\n      background: pink;\n    }\n  }\n}\n",".extends {\n  // Placeholder\n  %button {\n    display: block;\n    padding: 1em 4em;\n    text-transform: uppercase;\n    text-decoration: none;\n    border: 2px solid black;\n  }\n\n  .button {\n    @extend %button;\n  }\n\n  .primary-button {\n    // @extend Добавляет свойства из класса или placeholder\n    @extend %button;\n    background: lightcoral;\n  }\n\n  .secondary-button {\n    @extend %button;\n    background: lightblue;\n  }\n\n  .link {\n    color: green;\n\n    &:hover {\n      color: blue;\n    }\n  }\n\n  /* Ошибка! нельзя продлевать селекторы находящиеся вне медиа выражений\n  @media (min-width: 900px) {\n    a {\n      @extend .link;\n    }\n  } */\n}\n",".mixins {\n  @mixin mixin-name {\n    coolor: red;\n  }\n\n  @mixin border-radius($br: 4px) {\n    border-radius: $br;\n  }\n\n  @mixin box-shadow($vo: 0, $ho: 0, $blur: 10px, $spread: 0, $color: black) {\n    box-shadow: $vo $ho $blur $spread $color;\n  }\n\n  .button {\n    @mixin button($color: black, $bg: white) {\n      display: inline-block;\n      padding: 0.75em 1.5em;\n      text-decoration: none;\n      color: $color;\n      background: $bg;\n      border: 2px solid black;\n      @include border-radius(10px);\n      @include box-shadow($color: $bg);\n    }\n\n    @include button;\n\n    &--primary {\n      @include button(black, pink);\n      @include border-radius;\n      @include box-shadow($color: pink);\n    }\n    &--secondary {\n      @include button(black, yellow);\n      @include border-radius(20px);\n      @include box-shadow(0, 0, 10px, 0, $color: yellow);\n    }\n  }\n}\n","// Sass Functions\n\n$color: #6df;\n\n.functions {\n  > * {\n    padding: 1rem;\n  }\n\n  > * + * {\n    margin-top: 1rem;\n  }\n\n  .complement {\n    $color: red;\n\n    &__color {\n      padding: 0.5rem;\n      background-color: $color;\n      color: complement($color);\n    }\n\n    background-color: complement($color);\n  }\n\n  .rgba {\n    background: rgba(black, 0.1);\n  }\n\n  .darken {\n    background: lighten($color, 20);\n    color: darken($color, 80);\n  }\n\n  .mix {\n    background: darken($color, 50);\n    color: mix($color, white, 50);\n  }\n\n  .adjust-hue {\n    background: linear-gradient(0, adjust-hue($color, 25deg), $color);\n  }\n}\n","@use \"../layout/media\" as *;\n\n@import url(\" https:fonts.googleapis.com/css2?family=Raleway:wght@300;4\");\n\n$c-primary-500: #72b01d;\n\n$c-gray-100: rgba(0, 0, 0, 0.1);\n$c-gray-400: rgba(0, 0, 0, 0.35);\n$c-gray-500: rgba(0, 0, 0, 0.5);\n\n$c-white-500: #fff;\n$c-white-600: #f3f4f6;\n$c-white-700: #e4e4e4;\n\n$font-main: \"Raleway\", sans-serif;\n$font-heading: 700 48px $font-main;\n$font-title: 700 24px $font-main;\n$font-list-item: 400 24px $font-main;\n$font-pricing: 300 24px $font-main;\n$font-pricing-big: 700 72px $font-main;\n$font-button: 700 16px $font-main;\n\n$drop-shadow: 1px 10px 40px -3px $c-white-700;\n\n$grid-gutter: 1.5rem;\n\n.pricing-table-section {\n  margin-top: 1.5rem;\n  background-color: $c-white-600;\n}\n\n.pricing-table {\n  > * + * {\n    margin-top: 1.5rem;\n  }\n\n  &__header {\n    font: $font-heading;\n    text-align: center;\n  }\n\n  &__wrapper {\n    display: flex;\n    flex-wrap: wrap;\n    align-items: center;\n    margin-left: calc(#{$grid-gutter} * -0.5);\n    margin-right: calc(#{$grid-gutter} * -0.5);\n    list-style: none;\n    padding: 0;\n  }\n\n  &__item {\n    flex: 1 0 calc(100% - #{$grid-gutter});\n    order: 1;\n    margin-left: calc(#{$grid-gutter} * 0.5);\n    margin-right: calc(#{$grid-gutter} * 0.5);\n    margin-bottom: calc(#{$grid-gutter});\n\n    @include mq-up(md) {\n      flex: 1 0 calc(100% / 2 - #{$grid-gutter});\n    }\n\n    @include mq-up(lg) {\n      flex: 1 0 calc(100% / 3 - #{$grid-gutter});\n    }\n\n    &--first {\n      order: 0;\n      @include mq-up(md) {\n        flex-basis: calc(100% - #{$grid-gutter});\n\n        order: 0;\n      }\n\n      @include mq-up(lg) {\n        flex: 1 0 calc(100% / 3 - #{$grid-gutter});\n        order: 1;\n      }\n    }\n  }\n}\n\n.pricing-table-item {\n  display: flex;\n  flex-direction: column;\n  justify-content: flex-start;\n  align-items: center;\n  padding-top: 5rem;\n  padding-bottom: 5rem;\n\n  background-color: $c-white-500;\n  box-shadow: $drop-shadow;\n  border-radius: 2rem;\n\n  list-style: none;\n\n  &--primary {\n    background-color: $c-primary-500;\n\n    @include mq-up(lg) {\n      padding-top: 7rem;\n      padding-bottom: 7rem;\n    }\n\n    & .pricing-table-item__header {\n      color: $c-white-600;\n    }\n\n    & .pricing-table-item__features-item {\n      color: $c-white-600;\n      border-color: $c-white-700;\n    }\n\n    & .pricing-table-item__price {\n      color: $c-white-600;\n    }\n\n    & .pricing-table-item__link {\n      background: $c-white-600;\n      color: $c-gray-500;\n    }\n  }\n\n  > * + * {\n    margin-top: 1.625rem;\n  }\n\n  &__header {\n    font: $font-title;\n    text-transform: uppercase;\n  }\n\n  &__features-list {\n    display: flex;\n    width: 70%;\n    max-width: 25rem;\n    flex-direction: column;\n    align-items: center;\n    padding: 0;\n    list-style: none;\n\n    > * {\n      flex: 1 0 auto;\n      margin-bottom: 1.5rem;\n    }\n  }\n\n  &__features-item {\n    padding-bottom: 1.5rem;\n    min-width: 100%;\n\n    font: $font-list-item;\n    text-align: center;\n\n    border-bottom: 1px solid $c-white-700;\n    border-width: 2px;\n\n    &:last-child {\n      border: none;\n    }\n  }\n\n  &__price {\n    color: $c-primary-500;\n  }\n\n  &__price-money {\n    font: $font-pricing-big;\n  }\n\n  &__price-duration {\n    font: $font-pricing;\n  }\n\n  &__link {\n    margin-top: 5.365rem;\n    padding: 1.625rem 2.3rem;\n    font: $font-button;\n    letter-spacing: 0.15em;\n    text-decoration: none;\n    text-transform: uppercase;\n    color: $c-white-700;\n    background: $c-gray-500;\n    border-radius: 4px;\n    will-change: transform;\n    transition: transform 0.2s ease;\n\n    &:focus,\n    &:hover {\n      box-shadow: $drop-shadow;\n      outline: none;\n      transform: scale(1.22);\n    }\n\n    &:target {\n    }\n\n    &:hover {\n    }\n\n    &:active {\n      color: $c-white-500;\n      background: $c-primary-500;\n      outline: 1px solid $c-gray-100;\n    }\n  }\n}\n","@use \"sass:math\";\n\n$grid-breakpoints: (\n  xs: 0,\n  sm: 576px,\n  md: 768px,\n  lg: 992px,\n  xl: 1200px,\n  xxl: 1400px,\n);\n\n$container-max-widths: (\n  sm: 540px,\n  md: 720px,\n  lg: 960px,\n  xl: 1140px,\n  xxl: 1320px,\n);\n\n$container-padding: 1.5rem;\n\n$debug: false;\n$debug-prefix: if($debug, --padding, \"\");\n\n@function mq-min($name, $breakpoints: $grid-breakpoints) {\n  $min: map-get($breakpoints, $name);\n  @return if($min != 0, $min, null);\n}\n\n@function infix($name, $breakpoints: $grid-breakpoints) {\n  @return if(mq-min($name, $breakpoints) == null, \"\", \"-#{$name}\");\n}\n\n@mixin mq-up($name, $breakpoints: $grid-breakpoints) {\n  $min: mq-min($name, $breakpoints);\n  @if $min {\n    @media (min-width: $min) {\n      @content;\n    }\n  } @else {\n    @content;\n  }\n}\n\n// @mixin make-container() {\n//   display: block;\n//   margin-left: auto;\n//   margin-right: auto;\n//   padding-left: $container-padding;\n//   padding-right: $container-padding;\n//   max-width: 100%;\n// }\n\n// .container#{$debug-prefix} {\n//   @include make-container();\n// }\n\n// @each $breakpoint, $breakpoint-width in $container-max-widths {\n//   .container#{infix($breakpoint, $grid-breakpoints)} {\n//     @extend .container#{$debug-prefix};\n//   }\n\n//   @include mq-up($breakpoint, $grid-breakpoints) {\n//     %container-#{$breakpoint} {\n//       --width: calc(#{$breakpoint-width} / 2);\n//       padding-left: calc(50% - var(--width));\n//       padding-right: calc(50% - var(--width));\n//     }\n//     $extend-breakpoint: true;\n\n//     @each $name, $width in $grid-breakpoints {\n//       @if $extend-breakpoint {\n//         .container#{infix($name, $grid-breakpoints)} {\n//           @extend %container-#{$breakpoint};\n//         }\n//       }\n//       @if $name == $breakpoint {\n//         @debug 2 $name $breakpoint;\n//         $extend-breakpoint: false;\n//       }\n//     }\n//   }\n// }\n",".mixins-practice {\n  @mixin gradient($color-1, $color-2, $deg: 0) {\n    background-image: linear-gradient($deg, $color-1, $color-2);\n  }\n\n  @mixin grainy-gradient($color-1, $color-2, $deg: 0) {\n    filter: contrast(200%) brightness(100%);\n    background-image: linear-gradient($deg, $color-1, $color-2),\n      url(\"data:image/svg+xml,%3Csvg viewBox='0 0 800 800' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noiseFilter'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.65' numOctaves='3' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%25' height='100%25' filter='url(%23noiseFilter)'/%3E%3C/svg%3E\");\n  }\n\n  @mixin pseudo($width, $height, $background: black) {\n    &::after {\n      content: \"\";\n      width: $width;\n      height: $height;\n      background-color: $background;\n    }\n  }\n  h1 {\n    text-align: center;\n  }\n  .section {\n    font-size: 3rem;\n\n    text-align: center;\n    padding: 25vh 0;\n\n    $pseudo-color: black;\n\n    &--one {\n      @include gradient(#000000, #ff0000, 45deg);\n    }\n\n    &--two {\n      $color: rgba(255, 255, 255, 0.603);\n      @include grainy-gradient(#000092, #00000000, 0deg);\n      color: $color;\n\n      .section__title {\n        @include pseudo($width: 100px, $height: 5px, $background: $color);\n      }\n    }\n\n    &--three {\n    }\n\n    &__title {\n      display: flex;\n      flex-direction: column;\n      align-items: center;\n      @include pseudo($width: 100px, $height: 5px, $background: $pseudo-color);\n    }\n    &__button {\n      color: inherit;\n      text-decoration: none;\n    }\n  }\n}\n","@use \"../layout/media\" as *;\n\n$gutter: 1.5rem;\n$color-white-500: white;\n$color-white-400: rgb(240, 240, 240);\n$color-red-400: #ff715b;\n$color-green-200: #11998e;\n$color-green-300: #38ef7d;\n$color-black-300: rgba(0, 0, 0, 0.5);\n$color-black-400: #000000;\n$color-gray-400: #9d9d9d;\n\n$font-title: 900 4.35rem/0.798 Raleway, sans;\n$font-price: 300 1.875rem/1.25 Raleway, sans;\n$font-price-big: 300 3.38rem/3.8125rem Raleway, sans;\n$font-desc: 500 1.5rem/1.5 Raleway, sans;\n$font-button: 500 1.125rem/2 Raleway, sans;\n\n.item-card-practice {\n  .items {\n    display: flex;\n    flex-wrap: wrap;\n    margin-left: calc(#{$gutter} * -0.5);\n    margin-right: calc(#{$gutter} * -0.5);\n    padding: 1.5rem 0;\n    list-style: none;\n\n    &__card {\n      flex: 1 1 calc(100% - #{$gutter});\n      margin-left: calc(#{$gutter} * 0.5);\n      margin-right: calc(#{$gutter} * 0.5);\n\n      @include mq-up(md) {\n        flex: 1 1 calc(100% / 2 - #{$gutter});\n      }\n\n      @include mq-up(lg) {\n        flex: 1 1 calc(100% / 3 - #{$gutter});\n      }\n    }\n  }\n\n  .card {\n    padding-top: 256px;\n    @include mq-up(md) {\n      padding-top: 0;\n      padding-left: min(15%, 121px);\n    }\n\n    &__wrapper {\n      position: relative;\n      padding-top: 128px;\n      padding-bottom: 45px;\n      padding-left: 5%;\n      padding-right: 5%;\n\n      min-height: 512px;\n\n      background-image: linear-gradient(\n        110deg,\n        hsl(359deg 35% 76%) 0%,\n        hsl(359deg 26% 70%) 7%,\n        hsl(359deg 20% 65%) 14%,\n        hsl(359deg 15% 59%) 21%,\n        hsl(359deg 12% 54%) 29%,\n        hsl(359deg 10% 48%) 36%,\n        hsl(359deg 10% 43%) 43%,\n        hsl(359deg 10% 38%) 50%,\n        hsl(359deg 10% 33%) 57%,\n        hsl(359deg 9% 28%) 64%,\n        hsl(359deg 9% 23%) 71%,\n        hsl(359deg 8% 19%) 79%,\n        hsl(359deg 7% 14%) 86%,\n        hsl(359deg 4% 10%) 93%,\n        hsl(0deg 0% 5%) 100%\n      );\n      border-radius: 25px;\n      box-shadow: 7.6px 4.8px 29px 0 rgba(0, 0, 0, 0.21);\n\n      @include mq-up(md) {\n        padding-top: 55px;\n        padding-left: min(25%, 256px);\n      }\n    }\n    &__title {\n      font: $font-title;\n      color: $color-white-500;\n      letter-spacing: 4.16px;\n      text-transform: uppercase;\n      word-break: break-word;\n    }\n\n    &__preview {\n      position: absolute;\n      top: -200px;\n      left: 10%;\n      transform: translate();\n      width: 80%;\n      max-width: 256px;\n      z-index: 1;\n\n      @include mq-up(md) {\n        max-width: 35%;\n        top: 10%;\n        left: -15%;\n      }\n    }\n\n    &__image {\n      display: block;\n      max-width: 100%;\n      height: auto;\n\n      object-fit: cover;\n      object-position: bottom;\n      filter: drop-shadow(5px 5px 5px #222);\n\n      animation: spin 2s ease-in;\n\n      @keyframes spin {\n        0% {\n          transform: rotate3d(0);\n        }\n\n        50% {\n          transform: rotate3d(0, 1, 0, 180deg);\n        }\n\n        100% {\n          transform: rotate3d(0, 1, 0, 0deg);\n        }\n      }\n    }\n\n    &__price-wrapper {\n      display: flex;\n      align-items: flex-end;\n      margin-top: 50px;\n\n      font: $font-price;\n      color: $color-white-500;\n      letter-spacing: 3.6px;\n    }\n\n    &__price {\n      &--big {\n        font: $font-price-big;\n      }\n    }\n\n    &__desc {\n      margin-top: 37px;\n      font: $font-desc;\n      color: $color-white-400;\n    }\n\n    &__button-wrapper {\n      display: flex;\n      justify-content: flex-start;\n\n      flex-wrap: wrap;\n      margin-top: 40px;\n      margin-bottom: -10px;\n    }\n\n    &__button {\n      margin: 0;\n      padding-right: 32px;\n      padding-left: 32px;\n      margin-top: 0.6875rem;\n      margin-right: 0.6875rem;\n      padding-top: 8px;\n      padding-bottom: 9px;\n\n      font: $font-button;\n      color: $color-white-500;\n      letter-spacing: 1.8px;\n\n      border: 4px solid $color-white-400;\n      border-radius: 15px;\n      background: $color-white-400;\n\n      transition: transform 0.1s ease-in-out;\n\n      &:hover {\n        color: $color-black-400;\n      }\n\n      &--primary {\n        background-color: $color-red-400;\n        border-color: $color-red-400;\n      }\n\n      &:active {\n        transform: scale(0.96);\n      }\n    }\n  }\n}\n","@use \"./layout\";\n@use \"./components\";\n\n@use \"./lessons\";\n@use \"./practice\";\n\nhtml {\n  font-family: -apple-system, BlinkMacSystemFont, \"Segoe UI\", Roboto, Oxygen,\n    Ubuntu, Cantarell, \"Open Sans\", \"Helvetica Neue\", sans-serif;\n}\n\n.flow {\n  & > * + * {\n    margin-top: 1rem;\n  }\n}\n"],"sourceRoot":""}]);
